<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
   <el-form-item label="生产计划">
          <el-select v-model="listQuery.product_plan_code" placeholder="请选择">
            <el-option
              v-for="(item, index) in ProductPlanCodeNameList"
              :key="index + ' search'"
              :label="item.product_plan_name"
              :value="item.product_plan_code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="物料名称">
          <el-select v-model="listQuery.matter_code" placeholder="请选择">
            <el-option
              v-for="(item, index) in MatterCodeNameList"
              :key="index + ' search'"
              :label="item.matter_name"
              :value="item.matter_code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="检验结果">
          <el-select v-model="listQuery.test_result" placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option label="合格" value="合格"></el-option>
            <el-option label="不合格" value="不合格"></el-option>
          </el-select>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>过站查询</span>
      </div> -->
      <div class="">
       
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MissStationSearch",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        matter_code: "",
        user_code: "",
        work_code: "",
        test_result: "",
        product_plan_code: "",
        product_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        matter_code: "",
        user_code: "",
        work_code: "",
        test_result: "",
        product_plan_code: "",
        product_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 表格列
      tableCol: [
        {
          label: "生产计划",
          field: "product_plan_name",
          align: "center",
        },
        {
          label: "物料编码",
          field: "matter_id",
          align: "center",
        },
        {
          label: "物料名称",
          field: "matter_name",
          align: "center",
        },
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "人员",
          field: "user_name",
          align: "center",
        },
        {
          label: "检验结果",
          field: "test_result",
          align: "center",
          
          colType: "tag",
          tagRender: (row) => {
            
            if(row.test_result=="PASS"){
              row.test_result="合格"
            }else if(row.test_result=="FAIL"){
              row.test_result="不合格"
            }
            let params = {
              tagType: "",
              tagText: row.test_result,
            };
            if (row.test_result == "合格") {
              params.tagType = "";
            } else {
              params.tagType = "warning";
            }
            return params;
          },
        },
        // {
        //   label: "进站时间",
        //   field: "enter_time",
        //   align: "center",
        // },
        {
          label: "出站时间",
          field: "out_time",
          align: "center",
        },
      ],
      tableData: [],
    };
  },
  computed: {
    ...mapState([
      "ProductPlanCodeNameList",
      "PersonCodeNameList",
      "MatterCodeNameList",
      "CurrentUserInfo",
      "WorkCodeNameList",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticProductPlanCodeName",
      "getLogisticPersonCodeName",
      "getLogisticMatterNameCode",
      "getLogisticWorkCodeName",
    ]),

    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticProductTransitInfo(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticProductTransitInfo(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticProductTransitInfo(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticProductTransitInfo(this.listQuery);
    },
    // 过站查询 查询
    getLogisticProductTransitInfo(params) {
      baseManagement.getLogisticProductTransitInfo(params).then((res) => {
        console.log("过站查询 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.ProductPlanCodeNameList.forEach((CodeNameListItem) => {
              if (
                tableDataItem.product_plan_code ==
                CodeNameListItem.product_plan_code
              ) {
                tableDataItem.product_plan_name =
                  CodeNameListItem.product_plan_name;
              }
            });

            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.user_code == CodeNameListItem.user_code) {
                tableDataItem.user_name = CodeNameListItem.user_name;
              }
            });
            this.MatterCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.matter_code == CodeNameListItem.matter_code) {
                tableDataItem.matter_name = CodeNameListItem.matter_name;
              }
            });
            this.WorkCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.work_code == CodeNameListItem.work_code) {
                tableDataItem.work_name = CodeNameListItem.work_name;
              }
            });
          });
          this.tableData = tableData;
          console.log("tableData tableData", tableData);
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },


  },
  mounted() {
    
      // 生产计划内码和名称接口 查询
      this.getLogisticProductPlanCodeName();
      // 人员内码和人员名称 查询
      this.getLogisticPersonCodeName();
      // 物料内码和物料名称 查询
      this.getLogisticMatterNameCode();
      // 工站内码和工站名称接口： 查询
      this.getLogisticWorkCodeName();
    // 过站查询 查询
    this.getLogisticProductTransitInfo(this.listQuery);
  },
};
</script>

